.u-material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

.u-transition { transition: all .4s ease; }

@mixin placeholder {
  &::-webkit-input-placeholder { @content }
  &:-moz-placeholder { @content }
  &::-moz-placeholder { @content }
  &:-ms-input-placeholder { @content }
}

// Form Reset



input[type="text"],
input[type="email"],
input[type="date"],
input[type="phone"],
input[type="password"] {
    border: 0;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.form {
input[type="text"],
input[type="email"],
input[type="date"],
input[type="phone"],
input[type="password"],
textarea {
    text-align: left;
    border: 0;
    background-color: rgba(black, .1);
    width: 100%;
    padding: 10px 15px;
    margin-bottom: 15px;
    @extend .u-transition;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="date"]:focus,
input[type="phone"]:focus,
input[type="password"]:focus,
textarea:focus {
    background-color: rgba(black, .2);
    @extend .u-transition;
}

textarea { resize: vertical; }

input[type="checkbox"],
input[type="radio"] {
    display: none;
}

input:disabled + label {
    opacity: .4;
}

button, 
input[type='button'],
input[type='reset'],
input[type="submit"] {
    @extend .button;
    border: 0;
}

input[type="file"] { @extend .button; }

button {
    border-radius: 0;
    padding: 17px 20px;
}

fieldset {
    border: 0;
    padding: 0;
    margin: 5px 0;
}

legend {

}


form,
.form {
    padding: 10px 0;

    ::-webkit-input-placeholder {
        color: black;
        text-align: left;
    }
    :-moz-placeholder {
        color: black;
        text-align: left;
    } /* Firefox 18- */
    ::-moz-placeholder {
        color: black;
        text-align: left;
    } /* Firefox 19+ */
    :-ms-input-placeholder {
        color: black;
        text-align: left;
    }

    &--negative {
        input { color: white; }

        input[type='text'], input[type='email'], input[type='submit'], textarea {
            background-color: rgba(white, .1);
            @include placeholder {
                color: white;
            }
            &:focus {
                background-color: rgba(white, .2);
                @extend .u-transition;
            }
        }

        input[type="submit"]:hover {
            background-color:  rgba(white, .2);
        }
    }

    .button {
      margin-bottom: 15px;
    }
}

.checkbox,
.radio {
    padding:7px 10px;
    margin-bottom: 10px;

    input[type="checkbox"] + label:before,
    input[type="radio"] + label:before {
        vertical-align: -6px;
        margin-right: 15px;
    }

    input[type="checkbox"] + label:before {
        @extend .u-material-icons;
        content: 'check_box_outline_blank';
    }

    input[type="checkbox"]:checked + label:before {
        @extend .u-material-icons;
        content: 'check_box';
    }

    input[type="radio"] + label:before {
        @extend .u-material-icons;
        content: 'radio_button_unchecked';
    }

    input[type="radio"]:checked + label:before {
        @extend .u-material-icons;
        content: 'radio_button_checked';
    }
}

.select {
    background-color: rgba(black, .1);
    text-align: left;
    padding: 0 15px;
    margin-bottom: 15px;
    position: relative;
    @extend .u-transition;

    &::before {
        @extend .u-material-icons;
        content: 'keyboard_arrow_down';
        position: absolute;
        right: 0;
        padding-top: 10px;
        margin: 0 auto 0 auto;
        width: 42px;
        height: 32px;
        color: black;
        z-index: -1
    }

    select {
        height: 42px;
        border: 0;
        border-radius: 0;
        margin: 0;
        background-color: transparent;
        width: 100%;
        -webkit-appearance: none;
        background-image: none;
        &:focus {
        }
    }

    &:hover {
        background-color: rgba(black, .2);
        @extend .u-transition;
    }
}
}
